@font-face {
    font-family: Montserrat;
    src: local('Montserrat Regular'), url('../../public/fonts/Montserrat-Regular.ttf') format(truetype);
}
html {
    height:100%;
    margin: 0;
    padding: 0;
}
body {
    margin: 0;
    padding: 0;
    min-height: 100%;
}

.highlighted-as-value {
    background-color: rgb(228, 212, 190);
    text-decoration: dotted;
    padding: 4px;
    border-radius: 3px;
}

.bold-text {
    font-weight: bold;
    font-weight: normal;
    font-style: normal;
}

body {
    margin: 0;
    // overflow: hidden;
 
    width: 100%;


  /* Hide scrollbar for IE, Edge and Firefox */
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

body:before {
    position: fixed;
    background-color: #dad4bb;
    box-shadow: inset 0 0 100px black;
    width:100%;
    height:100%;
    content:"";
    z-index: -2;
    pointer-events: none;
}

body::-webkit-scrollbar {
    display: none;
}

/* All delay classes will take half the time to start */
:root {
    --animate-delay: 0.1s;
  }

.dialog-text {
    margin: 1em;
    padding: .4em;
    font-size: 14pt;
    background-color: aliceblue;
    border: solid 1pt black;
    box-shadow: 0 0 12pt grey;
    overflow: hidden;
}

.dialog-window {
    max-width: 90%;
}

.variants {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    align-items: center;
    margin: auto;
}

.variant {
    display: block;
    width: 50em;
    min-height: 1em;
    padding: 7pt;
    border: 1px solid black;
    margin: 4pt 0;
    box-shadow: 0 0 12pt grey;
    color: #444b43;
    font-family: Montserrat;
}

.variant.locked {
    opacity: 0.5 !important;
    border-color: red;
    cursor: not-allowed;
}

.variant.locked:hover {
    background-color: #838470;
    color: red;
    cursor: not-allowed;
    text-decoration: line-through;
}

.variant:hover {
    background-color: #838470;
    color: black;
    cursor: pointer;
}

.dev-toolbar-footer {
    position: fixed;
    bottom: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
    opacity: .3;
}

.dev-toolbar-footer:hover {
    opacity: 1;
}

.dev-toolbar-footer button {
    display: block;
    border: 1px solid black;
    font-size: x-small;
    font-family: monospace;
}

// typing
.container-typing {
    display: inline-block;
  }
  

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
.typed-out {
    overflow: hidden;
    border-right: .15em solid orange;
    white-space: nowrap;
    font-size: 1.6rem;
    width: 0;
    animation: typing 1s forwards;
}

.popup-panel {
    position: fixed;
    min-height: 20%;
    max-height: 90%;
    overflow-y: auto;
    border-bottom: 2pt solid black;
    z-index: 1000;
    background:rgb(228, 212, 190);
    box-shadow: 0 0 50em black;
    width: 100%;
}

.panel-header {
    display: flex;
    border-bottom: 1pt solid black;
}

.panel-header p {
    font-family: Montserrat, sans-serif;
    font-size: 21pt;
    margin-left: 2em;
}

.invisible {
    display: none;
}

.panel-button {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 2pt;
    background-color: rgba(200, 200, 200, .05);
    border-width: 0;
    border-right: 1px solid grey;
    min-width: 10em;
    // border-radius: 42pt;
    margin: 4pt;
    cursor: pointer;
}

.panel-button-icon {
    /* margin-right: 2em; */
    width: 16pt;
}

.panel-button-span {
    height: 100%;
    display: inline;
}

.panel-button:hover {
    outline: 1px solid white;
    background-color: rgba(200, 200, 200, 0.4);;
}

.header-panel {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: baseline;
}

.panel-close-button {
    border: 0;
    background: none;
    margin: 15pt;
    cursor: pointer;
    opacity: 0.5;
}

.panel-close-button:hover {
    outline: 2px solid black;
    opacity: 1;
}

.fact-id {
    background:url('../../public/img/free-icon-chess-piece.png') 0 0 no-repeat;
    display:block;
    width: 20px;
    height: 24px;
    content: "";
    margin: 11pt 12pt;
}

.fact:hover {
    background-color:rgba(200, 200, 200, 0.4);

}

.fact {
    display: flex;
    flex-direction: row;
}